<template>
  <div id="MvDetail">

    <div class="video-container" >
      <div class="music-container">
        <video
          :src="mvUrl.url"
          controls
          autoplay="autoplay"
        ></video>
          <div class="video-info">
            <div class="name">{{mvDetail.name}} - {{mvDetail.artistName}}</div>
            <div class="count">播放量：{{mvDetail.playCount}}</div>
          </div>
      </div>
    </div>

    <div class="music-container">

       <!-- 大家都爱看 -->
       <div class="similarMv">
         <h3>大家都爱看</h3>
          <div class="mv-content">
               <mv-item
                  class="item"
                  v-for="(item, index) in similarMv"
                  :key="index"
                  :mvId="item.id"
                  :cover="item.cover"
                  :name="item.name"
                  :artistName="item.artistName"
                  :artists="item.artists"
                  :playCount="item.playCount"
                    >
                </mv-item>
          </div>
       </div>

       <!-- MV 简介 -->
       <div class="mv-info">
         <div class="info-header">
            <h3>视频简介</h3>
            <div class="singer">
              <span v-for="(item, index) in mvDetail.artists" :key="index">{{item.name}} </span>
              <span>发行时间：{{mvDetail.publishTime}}</span>
            </div>
         </div>
         <p class="desc">{{mvDetail.desc}}</p>
       </div>

       <!-- 评论框 -->
       <comment-input :count="mvDetail.commentCount"></comment-input>

       <!-- 评论 -->
      <comment
      class="item"
      v-for="(item, index) in commentList"
      :key="index"
      :avatarUrl="item.user.avatarUrl"
      :nickname="item.user.nickname"
      :content="item.content"
      :time="item.time"
      ></comment>

    </div>

  </div>
</template>
<script>
// 工具包
import { req_mv_url, req_similar_mv, req_mv_detail } from "../network/mv/index.js";
import { req_mv_comment } from "../network/comment/index.js";
import tools from "../tools/formatData.js";

// 组件包
import commentInput from "../components/commentInput";
import comment from "../components/comment";
import mvItem from "../components/item/mvItem";
export default {
  name: "Mv",
  components: {
    commentInput,
    comment,
    mvItem
  },
  data() {
    return {
      domObj: [],
      id: this.$route.query.id,
      mvUrl: {},
      similarMv: [],
      mvDetail: {},
      // 评论信息
      commentList: [],
      page: 0,
      commentNum: 20,

    };
  },
  methods: {
    init() {
        document.documentElement.scrollTop = 0;
        this.domObj = window;
        this.domObj.onscroll = () => {
            // 滚动视口高度(也就是当前元素的真实高度)
            let scrollHeight = document.documentElement.scrollHeight || document.body.scrollHeight;
            // 可见区域高度
            let clientHeight = this.domObj.innerHeight || document.documentElement.clientHeight || document.body.clientHeight;
            // 滚动条顶部到浏览器顶部高度
            let scrollTop = this.domObj.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
            if(clientHeight + Math.floor(scrollTop) == scrollHeight){
               this.getMvComment();
            }
        }
    },
    // 获取 Mv url
    getMvUrl() {
      this.$store.dispatch("saveLoadingFlag", true);
      let params = {
        id: this.id,
      };
      req_mv_url(this, params).then(res => {
        this.mvUrl = res.data;
        this.$store.dispatch("saveLoadingFlag", false);
      });
    },

    // 获取相似 mv
    getsimilarMv() {
      this.$store.dispatch("saveLoadingFlag", true);
      let params = {
        mvid: this.id,
      };
      req_similar_mv(this, params).then(res => {
        this.similarMv = res.mvs.slice(0,4);
        this.$store.dispatch("saveLoadingFlag", false);
      });
    },

    // 获取 Mv 信息
    getMvDetail() {
      this.$store.dispatch("saveLoadingFlag", true);
      let params = {
        mvid: this.id,
      };
      req_mv_detail(this, params).then(res => {
        this.mvDetail = res.data;
        this.$store.dispatch("saveLoadingFlag", false);
      });
    },

    // 获取 mv 评论
    getMvComment() {
      this.$store.dispatch("saveLoadingFlag", true);
      let params = {
        id: this.id,
        limit: this.commentNum,
        offset: this.page * this.commentNum,
      };
      req_mv_comment(this, params).then(res => {
        this.$store.dispatch("saveLoadingFlag", false);
        this.commentList = this.commentList.concat(res.comments);
        this.page++;
      });
    },

    // 跳转到 MV
    goMv(id) {
      console.log(id);
      let time = new Date().getTime();
      this.$router.push({
        path: `/mvdetail/${time}`,
        query: { id },
      });
    },
  },
  watch: {
    mvDetail: function() {
      let value = tools._tranNumber(this.mvDetail.playCount, 2);
      this.mvDetail.playCount = value;
    },
    similarMv: function() {
      this.similarMv.forEach((e, index, arr) => {
        let value = tools._tranNumber(e.playCount, 2);
        arr[index].playCount = value;
      });
    },
    commentList: function() {
      this.commentList.forEach((e,index, arr) => {
        let value = tools._tranDate(e.time);
        arr[index].time = value;
      })
    }
  },
  created() {
    this.getMvUrl();
    this.getsimilarMv();
    this.getMvDetail();
    this.getMvComment();
  },
  mounted() {
    this.init();
  },
  beforeDestroy() {
    // 移除监听事件
    this.domObj.onscroll = null;
  }
};
</script>
<style lang="scss">
@import "../assets/scss/music.scss";
#MvDetail {
  .video-container {
    margin-top: 20px;
    .music-container {
    
      video {
        width: 1220px;
        height:670px;
        display:block;
       
        margin:0px auto;
        
      }
      .video-info {
        border: 1px solid rgba(0, 0, 0, 1);
        padding: 10px;
        border-radius: 12px;
        @include flex(flex-start);
        margin-top: 20px;
        .name {
          @include font(30px, 40px, $colorC);
        }
        .count {
          margin-left: 20px;
          @include font(16px, 40px, $colorC);
        }
      }
    }
  }
  .music-container {
    .similarMv {
      margin-top: 88px;
      h3 {
        margin-top: 20px;
        @include font(28px, 30px, $colorC);
        font-weight: normal;
      }
      .mv-content {
        margin-top: 20px;
        height: 200px;
        @include flex();
        flex-wrap: wrap;
      }
    }

    .mv-info {
      margin-top: 60px;
      margin-bottom: 40px;
      .info-header {
        h3 {
          @include font(30px, 40px, $colorC);
          padding-left: 6px;
        }
        .singer {
          margin-top: 12px;
          border-bottom: 1px solid rgba($color: #000000, $alpha: 0.5);
          padding-bottom: 12px;
          span {
            display: inline-block;
            padding: 0px 10px;
            @include font(18px, 20px, $colorC);
          }
        }
      }
      .desc {
        margin-top: 12px;
        @include font(16px, 30px, $colorB);
        @include fontDHidden(4);
        height: 120px;
        text-align: justify;
      }
    }
  }
}
</style>
